import React, { Component } from "react";
import AdminNavbar from "../../../Components/AdminNavbar/AdminNavbar";
import {
  Card,
  Pagination,
  Row,
  Media,
  Container,
  Form,
  Button,
  Modal
} from "react-bootstrap";
import { withRouter } from "react-router-dom";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import Footer from "../../../Components/Footer/Footer";
import Header from "../../../Components/Sidebar/Header";
import Firebase from "../../../Firebase/Firebase";
import * as emailjs from "emailjs-com";

class Complaints extends Component {
  state = {
    category: "All",
    resolved: false,
    selectedMail: "",
    mailModal: false,
    subject: "Response",
    message: ""
  };
  redirectToVendor = async () => {
    this.props.history.push("/admin/vendor");
  };
  setMailModal = () => this.setState({ mailModal: !this.state.mailModal });
  sendMail = async (mail, subject, message) => {
    if (mail.trim() !== "" && subject.trim() !== "" && message.trim() !== "") {
      let templateParams2 = {
        from_name: "ibrahimnabi80@gmail.com",
        to_name: mail.trim(),
        subject: subject,
        message_html: message
      };
      emailjs.send(
        "reserve_contact",
        "template_KiH1F4zS",
        templateParams2,
        "user_aVbbjsfKbS0Qub1NMA29M"
      );
      alert("Mail has been sent");
    } else {
      alert("Please fill all field before submitting.");
    }
  };
  changeState = (name, value) => {
    this.setState({
      [name]: value
    });
  };
  render() {
    const complaintsToRender = this.props.complaints.map(element => {
      const customer = this.props.customers.find(
        c => c.uid === element.customer_uid
      );
      const vendor = this.props.vendors.find(
        c => c.uid === element.restaurant_uid
      );
      const filter = vendor
        ? vendor.category === this.state.category ||
          this.state.category === "All"
        : false;
      const condition = filter && element.is_resolved === this.state.resolved;
      return condition ? (
        <>
          <Media as="li">
            <img
              width={64}
              height={64}
              className="mr-3"
              style={{ objectFit: "cover" }}
              src={
                customer
                  ? customer.photo
                    ? customer.photo
                    : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
                  : "https://encrypted-tbn0.gstatic.com/images?q=tbn%3AANd9GcRwYsBVC-oKRvO87aXVfE63jYe52NcDYM5ArsCRKAGZaImUHAql"
              }
              alt="Generic placeholder"
            />
            <Media.Body>
              <h5>{customer ? customer.name : "Customer Deleted"}</h5>
              <Row className="ml-1">
                <h6 onClick={this.redirectToVendor}>
                  {vendor ? vendor.name : "Vendor Deleted"}
                </h6>
              </Row>
              <Row className="ml-1">
                <h6 className="text-muted">{element.title}</h6>
              </Row>
              <p>{element.description}</p>
              <p>
                Date of Complaint:{" "}
                <small className="text-muted">{element.date}</small>
              </p>
              <Row>
                <Button
                  onClick={() =>
                    Firebase.updateComplaintSeen(element.complaint_key, true)
                  }
                  variant={element.isSeen ? "success" : "primary"}
                  disabled={element.isSeen}
                >
                  {element.isSeen ? "Seen" : "Set as Seen"}
                </Button>
                <Button
                  onClick={async () => {
                    await Firebase.updateComplaintResolved(
                      element.complaint_key,
                      true
                    );
                    await Firebase.updateComplaintSeen(
                      element.complaint_key,
                      true
                    );
                    if (customer && vendor) {
                      await this.sendMail(
                        customer.email,
                        "Complaint Resolved",
                        "The complaint you made on " +
                          element.date +
                          ", about " +
                          vendor.name +
                          ", has been resolved. We value your feedback and our team is always trying our best to resolve complaints. Thank you for using our application."
                      );
                      await this.sendMail(
                        vendor.email,
                        "Complaint Resolved",
                        "The complaint made against you at " +
                          element.date +
                          ", bt " +
                          customer.name +
                          ", has been marker as resolved by us. Thank you for co-operating and partnering with us."
                      );
                    }
                  }}
                  disabled={element.is_resolved}
                  variant={element.is_resolved ? "success" : "primary"}
                  className="ml-3"
                >
                  {element.is_resolved ? "Resolved" : "Set as Resolved"}
                </Button>
                <Button
                  className="ml-3"
                  onClick={() => {
                    if (customer) {
                      this.setMailModal();
                      this.setState({
                        selectedMail: customer.email
                      });
                    }
                  }}
                >
                  Send Custom Mail to Customer
                </Button>
                <Button
                  onClick={() => {
                    if (vendor) {
                      this.setMailModal();
                      this.setState({
                        selectedMail: vendor.email
                      });
                    }
                  }}
                  className="ml-3"
                >
                  Send Custom Mail to Vendor
                </Button>
              </Row>
            </Media.Body>
          </Media>
          <hr></hr>
        </>
      ) : null;
    });
    return (
      <div>
        <Header>
          <Container>
            <Card className="my-3">
              <Card.Header>
                <h1>Complaints</h1>
              </Card.Header>

              <Card.Body>
                <Form.Group controlId="exampleForm.ControlSelect1">
                  <Form.Label>Vendor Type</Form.Label>
                  <Form.Control
                    onChange={e => this.setState({ category: e.target.value })}
                    as="select"
                  >
                    <option value="All">All</option>
                    <option value="Restaurants">Restaurants</option>
                    <option value="Cafes">Cafes</option>
                    <option value="Events">Events</option>
                    <option value="Escape Rooms">Escape Rooms</option>
                  </Form.Control>
                </Form.Group>
                <Form.Row>
                  <Form.Group className="ml-4" controlId="formBasicCheckbox">
                    <Form.Check
                      onClick={e => {
                        this.setState({
                          resolved: e.target.checked
                        });
                      }}
                      type="checkbox"
                      label="Show Resolved Complaints"
                    />
                  </Form.Group>
                </Form.Row>
                <ul className="list-unstyled mt-5">{complaintsToRender}</ul>
              </Card.Body>
            </Card>
          </Container>

          <Footer></Footer>
        </Header>
        <Modal
          show={this.state.mailModal}
          size="lg"
          onHide={this.setMailModal}
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Header>
            <h4>Send Custom Mail</h4>
          </Modal.Header>
          <Modal.Body>
            <div className="form-group">
              <label for="exampleFormControlSelect1">Select Subject</label>
              <select
                onChange={e => this.changeState("subject", e.target.value)}
                className="form-control"
                id="exampleFormControlSelect1"
              >
                <option value="Response">Response</option>
                <option value="Inquiry">Inquiry</option>
              </select>
            </div>
            <div className="form-group">
              <label for="exampleFormControlTextarea1">
                Enter Your Message
              </label>
              <textarea
                onChange={e => this.changeState("message", e.target.value)}
                className="form-control"
                id="exampleFormControlTextarea1"
                rows="3"
                placeholder="Message"
              ></textarea>
            </div>
          </Modal.Body>
          <Modal.Footer>
            <Button
              variant="success"
              onClick={async () => {
                await this.sendMail(
                  this.state.selectedMail,
                  this.state.subject,
                  this.state.message
                );
                this.setMailModal();
              }}
            >
              Send
            </Button>
            <Button variant="danger" onClick={this.setMailModal}>
              Close
            </Button>
          </Modal.Footer>
        </Modal>
      </div>
    );
  }
}
export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Complaints));
