import React, { Component } from "react";
import { Container, Row, Col, Card, Button } from "react-bootstrap";
import { withRouter } from "react-router-dom";
import Firebase from "../../../Firebase/Firebase";
import { setVendor } from "../../../Config/localStorage";
import { connect } from "react-redux";
import {
  vendorMapStateToProps,
  vendorMapDispatchToProps
} from "../../../Config/VendorMapping";
class RegistrationOptions extends Component {
  googleLogin = async () => {
    const response = await Firebase.loginVendorGoogle();
    if (response.message === "User Found") {
      this.props.updateLogin(response.result);
      setVendor(response.result);
      this.props.history.push("/vendor");
    } else {
      this.props.updateRegistrationSocial({
        name: response.result.displayName,
        email: response.result.email,
        uid: response.result.uid,
        photoUrl: response.result.photoURL
      });
      this.props.history.push("/vendor/registersocial");
    }
  };
  register = async () => {
    this.props.history.push("/vendor/register");
  };
  render() {
    return (
      <div>
        <Container fluid>
          <Row>
            <Col
              className="d-flex flex-column justify-content-center align-items-center"
              xs="12"
              style={{
                padding: 50,
                paddingTop: "10vh",
                paddingBottom: "10vh",
                backgroundColor: "#ba0fb4",
                minHeight: "100vh",
                justifyContent: "center",
                alignItems: "center"
              }}
            >
              <Card className="w-50">
                <Card.Body>
                  <div className="d-flex flex-column justify-content-center align-items-center w-100">
                    <Row>
                      <Col xs="12">
                        <Button
                          onClick={this.register}
                          block
                          variant="info"
                          type="button"
                        >
                          Continue with Email
                        </Button>
                      </Col>
                      <Col xs="12" className="mt-4">
                        <Button
                          onClick={this.googleLogin}
                          block
                          className="google bg-success"
                          type="button"
                        >
                          Continue with Google
                        </Button>
                      </Col>
                      <Col xs="12" className="mt-4">
                        <Button
                          onClick={this.register}
                          block
                          variant="primary"
                          type="button"
                        >
                          Continue with Phone number
                        </Button>
                      </Col>
                    </Row>
                  </div>
                </Card.Body>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default connect(
  vendorMapStateToProps,
  vendorMapDispatchToProps
)(withRouter(RegistrationOptions));
