const setAdmin = user => {
  const userToSet = {
    ...user,
    loginTime: Date.now()
  };
  localStorage.setItem("admin", JSON.stringify(userToSet));
};
const getAdmin = () => {
  const user = JSON.parse(localStorage.getItem("admin"));
  return user;
};
const removeAdmin = () => {
  localStorage.removeItem("admin");
};
const setVendor = user => {
  const userToSet = {
    ...user,
    loginTime: Date.now()
  };
  localStorage.setItem("Vendor", JSON.stringify(userToSet));
};
const getVendor = () => {
  const user = JSON.parse(localStorage.getItem("Vendor"));
  const isExpired = user
    ? new Date() - user.loginTime > 1000 * 60 * 60 * 2
    : false;
  if (isExpired) {
    removeVendor();
    return false;
  }
  return user;
};
const removeVendor = () => {
  localStorage.removeItem("Vendor");
};
export { setAdmin, getAdmin, removeAdmin, setVendor, getVendor, removeVendor };
