import React, { Component } from "react";
import Header from "../../../Components/Sidebar/Header";
import {
  Container,
  Row,
  Col,
  Card,
  Form,
  Button,
  Table
} from "react-bootstrap";

import Footer from "../../../Components/Footer/Footer";
import { connect } from "react-redux";
import {
  mapStateToProps,
  mapDispatchToProps
} from "../../../Config/AdminMapping";
import { withRouter } from "react-router-dom";
import Firebase from "../../../Firebase/Firebase";
class Location extends Component {
  state = {
    locationName: "",
    locations: []
  };
  componentDidMount = async () => {
    await this.updateLocations();
  };
  updateLocations = async () => {
    const locations = await Firebase.getLocations();
    this.setState({
      locations
    });
  };
  addLocation = async () => {
    let condition = true;
    this.state.locations.forEach(
      name =>
        (condition =
          name.location === this.state.locationName ? false : condition)
    );
    console.log(condition);
    if (this.state.locationName !== "" && condition) {
      await Firebase.addLocation(this.state.locationName);
      await this.updateLocations();
      this.setState({
        locationName: ""
      });
    } else {
      if (this.state.locationName === "") {
        alert("Please enter a location name");
      } else {
        alert("Location name is already taken.");
      }
    }
  };
  handler = (name, value) => {
    this.setState({
      [name]: value
    });
  };
  render() {
    return (
      <div>
        {" "}
        <Header>
          <Container>
            <Row>
              <Col className="mt-3 mt-md-0 mb-5" xs="12">
                <Card>
                  <Card.Header>
                    <h1>Locations</h1>
                  </Card.Header>
                  <Card.Body>
                    <Form.Group controlId="formGridEmail">
                      <Form.Label>Location Name</Form.Label>
                      <Form.Control
                        onChange={e =>
                          this.handler("locationName", e.target.value)
                        }
                        type="text"
                        value={this.state.locationName}
                        placeholder="Enter Location Name"
                      />
                    </Form.Group>
                    <Button onClick={this.addLocation} block>
                      Add Location
                    </Button>
                    <hr></hr>
                    <Form.Group controlId="exampleForm.ControlSelect1">
                      <Form.Label>Locations Preview</Form.Label>
                      <Form.Control as="select">
                        {this.state.locations.map(l => (
                          <option value={l.location}>{l.location} </option>
                        ))}
                      </Form.Control>
                    </Form.Group>
                    <hr></hr>
                    <Table striped hover responsive>
                      <thead>
                        <tr>
                          <th>Location Name</th>
                          <th></th>
                        </tr>
                      </thead>
                      <tbody>
                        {this.state.locations.map(element => {
                          return (
                            <tr>
                              <td>{element.location}</td>

                              <td className="d-flex align-items-center justify-content-center">
                                <ion-icon
                                  class="blockButton text-danger"
                                  name="trash-outline"
                                  onClick={async () => {
                                    await Firebase.deleteLocation(element.key);
                                    await this.updateLocations();
                                  }}
                                ></ion-icon>
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </Table>
                  </Card.Body>
                </Card>
              </Col>
            </Row>
          </Container>
          <Footer></Footer>
        </Header>
      </div>
    );
  }
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(Location));
